
























































	import service from '../../service.js';
	import config from '../../util/config.js';
	import request from '../../util/request.js';
    import {
        mapState,
		mapMutations
    } from 'vuex'

    export default {
        computed: mapState(['forcedLogin', 'hasLogin', 'userType', 'userInfo']),
		data() {
		    return {
				// 阀组本周未检查数量
				bzwjcsl: '正在读取...',
				// 当天火警数
				dthjs: '正在读取...',
				// 当月未检查数量
				dywjcsl: '正在读取...',
			}
		},
		methods: {
		    ...mapMutations(['login']),
			// 扫一扫
			openScan() {
				var that = this;
				// 允许从相机和相册扫码
				uni.scanCode({
					success: function (res) {
// 						console.log('条码类型：' + res.scanType);
// 						console.log('条码内容：' + res.result);

// 						uni.showToast({
// 							icon: 'none',
// 							title: '条码内容：' + res.result
// 						});
						
						let param = {
							userid: that.userInfo.userid,
							dwbh: res.result,
						};
						request.requestLoading(config.scan, param, '正在加载', 
							function(res){
								// console.log('' + JSON.stringify(res));
								uni.navigateTo({
									url: '../pointAdd/pointAdd?obj=' + JSON.stringify(res)
								});
							},function(){
								uni.showToast({
									icon: 'none',
									title: '请求失败'
								});
							},function() {
								
							});
					}
				});
			},
			// 数据图表统计
			openCharts() {
				uni.navigateTo({
					url: '../mpvue-echarts/chartList'
				})
			},
			// 顶部数据统计
			getCount(message = "") {
				var that = this;
				let params = {
					userid: that.userInfo.userid
				};
				request.requestLoading(config.getMainPageCount, params, message, function(res){
					console.log('' + JSON.stringify(res));
					that.bzwjcsl = res.bzwjcsl;
					that.dthjs = res.dthjs;
					that.dywjcsl = res.dywjcsl;
				}, function(){
					that.bzwjcsl = "获取失败，点击重新获取";
					that.dthjs = "获取失败，点击重新获取";
					that.dywjcsl = "获取失败，点击重新获取";
				}, function(){
					
				})
			},
		},
        onShow() {
			this.login(service.getUsers());
            if (!this.hasLogin) {
                uni.showModal({
                    title: '未登录',
                    content: '您未登录，需要登录后才能继续',
                    /**
                     * 如果需要强制登录，不显示取消按钮
                     */
                    showCancel: !this.forcedLogin,
                    success: (res) => {
                        if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
                            if (this.forcedLogin) {
                                uni.reLaunch({
                                    url: '../login/login'
                                });
                            } else {
                                uni.navigateTo({
                                    url: '../login/login'
                                });
                            }
                        }
                    }
                });
            }else {
				if (this.userType == 1) {
					this.getCount();
				}
			}
        }
    }
